/* WeeklyFortune.css */
.weekly-fortune {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  color: white;
}

.form-header {
  text-align: center;
  margin-bottom: 30px;
}

.form-header h2 {
  font-size: 2.5em;
  margin-bottom: 10px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.form-header p {
  font-size: 1.2em;
  opacity: 0.9;
}

.coming-soon {
  background: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
  text-align: center;
}

.coming-soon-icon {
  font-size: 4em;
  margin-bottom: 20px;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-10px); }
  60% { transform: translateY(-5px); }
}

.coming-soon h3 {
  font-size: 2em;
  margin-bottom: 15px;
  color: #ffffff;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3);
}

.coming-soon p {
  font-size: 1.1em;
  line-height: 1.6;
  margin-bottom: 15px;
  opacity: 0.9;
}

.preview-features {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin: 20px 0;
  text-align: left;
}

.preview-features h4 {
  font-size: 1.3em;
  margin-bottom: 15px;
  color: #ffffff;
  text-align: center;
}

.preview-features ul {
  list-style: none;
  padding: 0;
}

.preview-features li {
  padding: 8px 0;
  font-size: 1em;
  opacity: 0.9;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.preview-features li:last-child {
  border-bottom: none;
}

.alternative-options {
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin-top: 20px;
}

.alternative-options h4 {
  font-size: 1.3em;
  margin-bottom: 10px;
  color: #ffffff;
}

.alternative-options p {
  margin: 0;
  font-size: 1.1em;
  line-height: 1.4;
}

/* 모바일 반응형 */
@media (max-width: 768px) {
  .weekly-fortune {
    margin: 10px;
    padding: 15px;
  }
  
  .form-header h2 {
    font-size: 2em;
  }
  
  .coming-soon {
    padding: 20px;
  }
  
  .coming-soon-icon {
    font-size: 3em;
  }
  
  .coming-soon h3 {
    font-size: 1.5em;
  }
} 